import React from "react";
import DeleteIcon from "@material-ui/icons/Delete";

function Item(props) {
  function ondelete() {
    props.delete(props.id);
  }

  return (
    <div className="item">
      <h3 id="itemheader">{props.Title}</h3>
      <p>{props.Content} </p>
      <button onClick={ondelete} id="deleteButton">
        <DeleteIcon />
      </button>
    </div>
  );
}

export default Item;
